<template>
  <div class="home">
    <div class="header">
      <div class="address_map" @click="$router.push({name:'address',params:{city}})">
        <span>{{address}}</span>
      </div>
      <div class="search_wrap" :class="{'fixedview':showFilter}" >
        <div class="shop_search">
        <i class="fa fa-search"></i>
        搜索商家 商家名称</div>
      </div>
    </div>
    <div id="container" >
      <mt-swipe :auto="4000" class="swiper">
        <mt-swipe-item v-for="(item,index) in swipeImgs" :key="index">
          <img :src="item" alt="">
        </mt-swipe-item>
      </mt-swipe>
      <mt-swipe :auto="0" class="entries">
        <mt-swipe-item v-for="(items,index) in entries" :key="index">
          <div class="foodentry" v-for="(item,index) in items" :key="index">
            <div class="img_wrap">
              <img :src="item.image" alt="">
            </div>
            <span>{{item.name}}</span>
          </div>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!-- 筛选 -->
    <div class="shoplist-title" v-show="entries">
      推荐商家
    </div>
    <!-- 筛选 -->
    <FilterView :filterData="filterData" @searchFixed="searchFixed"  @updata="updata"></FilterView>
    <mt-loadmore
      :top-method="loadTop"
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      :bottomPullText="bottomPullText"
      ref="loadmore"
    >
      <IndexShop
     v-for="(item,index) in restaurants"
     :key="index"
     :restaurant="item.restaurant"
    ></IndexShop>
    </mt-loadmore>
    
  </div>
  
</template>

<script>
import { Indicator,Swipe, SwipeItem,Loadmore   } from 'mint-ui';
import FilterView from "../components/FilterView";
import IndexShop from "../components/IndexShop"
export default {
  name: 'Home',
  data(){
    return{
      entries:[],
      swipeImgs:[],
      filterData:{},
      showFilter:false, 
      condition:null,//条件
      page:1,
      size:5,
      allLoaded:[],
      bottomPullText:"加载更多",
      restaurants:[]
    }
  },
  methods:{
    getData(){
      //获取轮播图和导航数据
      this.$axios("/api/profile/shopping").then(res=>{
        //console.log(res);
         this.entries=res.data.entries ;
        this.swipeImgs=res.data.swipeImgs
        Indicator.close();
      }).catch(err=>{
        console.log(err)
      });
      this.$axios("/api/profile/filter").then(res=>{
        //console.log(res.data);
        this.filterData=res.data
      }).catch(err=>{
        console.log(err)
      });
    },
    loadData(){
      this.page = 1;
      this.allLoaded=false;
      this.bottomPullText = "加载更多"
      this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`,this.condition)
      .then(res=>{
        console.log(res.data)
        this.$refs.loadmore.onTopLoaded();
        this.restaurants = res.data
      })
    },
    loadTop(){
      this.loadData();
    },
    loadBottom(){//上拉加载更多
      if(!this.allLoaded){
        this.page++;
        this.$axios
        .post(`/api/profile/restaurants/${this.page}/${this.size}`)
        .then(res=>{
          this.$refs.loadmore.onBottomLoaded();
          if(res.data.length>0){
            res.data.forEach(item => {
              this.restaurants.push(item)
            });
            if(res.data.length<this.size){
              this.allLoaded=true
              this.bottomPullText="我是有底线的"
            }
          }else{
            this.allLoaded = true;
            this.bottomPullText = "我是有底线的"
          }
        })
      }

    },
    searchFixed(isShow){
      this.showFilter = isShow
    },
    updata(condition){
      this.condition = condition;
      //console.log(condition)
      this.loadData();
    }
  },
  computed:{
        address(){
            return this.$store.getters.address
        },
        city(){
          //console.log(this.$store.getters.location.addressComponent.city)
          return this.$store.getters.location.addressComponent.city || this.$store.getters.location.addressComponent.province
        }
    },
  created(){
      Indicator.open('加载中...'),
      this.getData(); 
      this.loadData() 
  },
  components: {
    FilterView,
    IndexShop
  },
}
</script>

